<template>
  <div class="serviceTerm-container">
    <div class="text-container">
      <p>“一键康”是杭州势成科技有限公司（以下简称“势成科技”）专门为有健康需求的用户开发的手机应用，用户利用它可进行身体测评、体检预约、查看体检报告等操作。在注册成为“一键康”用户之前，您应确认已经阅读本用户协议，并同意本用户协议中陈列的所有条款。 如用户不同意本用户协议任意内容，请勿注册或使用“一键康”的服务。
        <b> 如用户通过进入注册程序并勾选“一键康用户协议”，即表示用户与势成科技已达成协议，自愿接受本用户协议的所有内容。此后，用户不得以未阅读本用户协议内容作任何形式的抗辩。</b>
      </p>
      <div>一、用户协议的确认</div>
      <p>一键康的服务涉及到的产品的所有权以及相关软件的知识产权归势成科技所有。本用户协议的法律效力范围及于一键康的一切产品和服务(包括但不限于本平台的全部产品及服务内容、一键康在本平台以外发布的服务信息、用户与势成科技签订的相关文件/协议/合同)，用户在享受一键康任何单项服务时，应当受本用户协议的约束。当用户使用一键康各单项服务时，应以一键康认可的方式同意该单项服务的用户协议以及一键康在该单项服务中发出的各类公告(下列简称单项条款)，在此情况下单项条款与本用户协议同时对用户产生效力。若单项条款与本用户协议存在同类条款的冲突，则在单项条款约束范围内应以单项条款为准。</p>
      <div>二、一键康在线使用规范</div>
      <p>1.您在一键康在线应用中的言行需遵守国家法律、法规等规范性文件及势成科技的各项规则和要求，不违背社会公共利益或公共道德，不损害他人的合法权益，不违反本用户协议及相关规则。如果因此产生任何法律后果的，您应以自己的名义独立承担法律责任。</p>
      <p>2.注册成功后，用户必须保护好自己的账号和密码，因本人泄露造成的任何损失需由用户本人承担。</p>
      <p>3.本软件仅限本人使用，如超出范围使用造成损失，本公司概不负责。</p>
      <p>4.“一键康”所登载的专有信息，未经书面授权，任何人不得以任何形式侵犯其著作权，包括擅自复制、链接、非法使用和转载。</p>
      <div>三、一键康服务</div>
      <p>（一）服务宗旨</p>
      <p>一键康主要为用户提供体检预约、健康咨询等服务。一键康旨在整合相关医疗资源，利用互联网的便利性，为用户提供专业、快捷的健康服务。</p>
      <p>（二）服务内容</p>
      <p>1.体检预约</p>
      <p>体检预约服务一键康提供的是体检套餐设计、体检机构安排、体检路线指引、检后建立以及维护体检档案等服务。</p>
      <p>用户应当仔细阅读体检预约订单页面中所包含的全部内容，包括但不限于体检信息中的全部内容、服务的有效期、退款条件等内容（如有），选择及确认用户接收电子付款凭证的联系方式等内容。前述预约页面中所包含的全部内容，构成了用户与医院之间达成的体检服务合同的合同内容，用户完全同意体检预约的全部内容后方可提交订单。</p>
      <p>用户提交体检预约订单即视为用户已知晓、同意并接受订单中的全部内容，与医院成立了体检服务合同。体检预约订单中所包含的全部内容即为体检服务合同的内容，具体包括但不限于体检服务的种类、价格、有效期、预约时间、医院地址等，用户与医院均应当按照前述体检服务合同的约定履行各自的权利义务。用户支付体检服务价款成功后，一键康向用户发送电子支付凭证，用户可按照体检服务合同的约定凭电子凭证向医院主张获得体检服务。</p>
      <p>
        <u>一键康只为用户提供用户认可的相关体检机构的预约工作，一键康本身不提供任何体检服务，一键康不对体检相关行为及出具的体检结果承担任何责任。若因体检机构原因造成用户误诊、数据错误等各种医疗差错及人身财产受到损害的各种事故，用户应当向体检机构提出请求，由此产生的一切纠纷均与一键康无关。</u>
      </p>
      <p>
        <u>用户在进行体检过程中，如发现体检服务与预约内容不符等其他问题的，应与医院采取协商或其他方式予以解决，一键康应积极协助用户与医院解决争议。</u>
      </p>
      <p>2.健康咨询指导</p>
      <p>一键康为用户提供健康咨询指导服务，同时用户应清楚的了解一键康为健康咨询服务机构而非医疗机构，一键康并不直接亦不间接为用户提供任何医疗服务(包括但不限于病情诊断、急诊、医学治疗、医学护理、提供药物、开具处方等)，如用户需要任何医疗服务，用户清楚地知悉应当前往医疗机构寻求相关服务。
        <b>一键康提供的健康指导服务仅以用户提供的体检报告等资料为依据，向用户提供不涉及任何医疗服务或医疗资质许可的保健指导和咨询服务。如果客户需要寻求医学诊疗以及服用药物，必须通过专业医疗机构进行。凡仅依照本平台提供的健康咨询指导服务造成的任何不良后果，均由客户自负责任。</b>
      </p>
      <div>四、隐私政策</div>
      <p>用户知悉并同意，为便于向用户提供更好的服务，一键康将在用户自愿选择服务或者提供信息的情况下收集用户的个人信息、健康档案信息并将这些信息进行整合。在用户使用一键康服务时，服务器会自动记录一些信息，包括但不限于URL、IP地址、浏览器类型、使用语言、访问日期和时间等。一键康收集的信息将成为一键康常规商业档案的一部分，且有可能因为转让、合并、收购、重组等原因而被转移到一键康的继任公司或者指定的一方。一键康同意善意使用收集的信息，且采取各项措施保证信息安全。尊重用户个人隐私是一键康的一项基本政策。所以，一键康不会公开或透露用户的注册资料及保存在一键康各项服务中的非公开内容，除非一键康在诚信的基础上认为透露这些信息在以下几种情况是必要的：</p>
      <p>1.用户授权一键康向相关合作医疗机构调取用户的电子版体检及相关医疗数据报告，为用户提供的相应的健康咨询指导服务；</p>
      <p>2.遵守有关法律规定，包括在国家有关机关查询时，提供用户的注册信息、用户在一键康的平台上发布的信息内容及其发布时间、互联网地址或者域名；</p>
      <p>3.保持维护一键康的知识产权和其他重要权利；</p>
      <p>4.在紧急情况下竭力维护用户个人和社会大众的隐私安全；</p>
      <p>5.根据本条款相关规定或者一键康认为必要的其他情况下。</p>
      <p>
        <u>一键康与第三方合作向用户提供相关的服务，在此情况下，如该第三方同意承担与一键康同等的保护用户隐私的责任，则一键康有权将用户信息提供给该第三方。</u>
      </p>
      <div>五、协议内容</div>
      <p>1.本用户协议内容包括协议正文及所有杭州势成科技有限公司已经发布的或将来可能发布的各单项条款。所有单项条款为本用户协议不可分割的组成部分，与本用户协议正文具有同等法律效力；若单项条款与本用户协议存在同类条款的冲突，则在单项条款约束范围内应以单项条款为准。</p>
      <p>
        <u>2.一键康有权在必要时通过在本平台上发出公告等合理方式修改本用户协议条款以及各单项服务的相关条款（如有）。用户在享受各项服务时，应当及时查阅了解修改的内容，并自觉遵守本用户协议条款以及各单项服务的相关条款。用户如继续使用本用户协议条款涉及的服务，则视为对修改内容的同意，当发生有关争议时，以最新的用户协议条款为准；用户在不同意修改内容的情况下，应停止使用本用户协议涉及的服务。
        </u>
      </p>
      <div>六、法律适用、管辖</div>
      <p>1.本用户协议之效力、解释、变更、执行与争议解决均适用中华人民共和国法律，如无相关法律规定的，则应参照通用国际商业惯例和（或）行业惯例。</p>
      <p>
        <u>2.因本用户协议引起的或与本用户协议有关的任何争议，均提请【】仲裁委员会按照该会仲裁规则进行仲裁。仲裁裁决是终局的，对双方均有约束力。</u>
      </p>
      <div>七、其他</div>
      <p>1.一键康不行使、未能及时行使或者未充分行使本条款或者按照法律规定所享有的权利，不应被视为放弃该权利，也不影响一键康在将来行使该权利。</p>
      <p>2.如本条款中的任何条款无论因何种原因完全或部分无效或不具有执行力，本用户协议的其余条款仍应有效且具有约束力，一键康及用户均应尽力使该条款设定的本意得到实现。</p>
    </div>
  </div>
</template>
<script>
  export default{
    name: 'serviceTerm'
  }
</script>
<style lang="scss" scoped>
.serviceTerm-container {
  padding: 0 0.5rem;
  font-size: 0.28rem;
  color: #787878;

  .text-container {
    div {
      margin: 0.4rem 0;
      color: #1c1c1c;
      font-weight: 600;
      font-size: 0.3rem;
    }
    b {
      color: #1c1c1c;
      font-weight: normal;
      line-height: 0.48rem;
    }
    p {
      line-height: 0.48rem;
      u {
        color: #1c1c1c;
      }
    }
  }
}
</style>



